import React from 'react'
import Popup from './Popup.jsx'
import { useState } from 'react'
import surecss from './../style/sure.css'

export default () => {
    var func = () => {
        var data = JSON.parse(localStorage.getItem('ficha_'));
        if ((data.nome_do_jogador && data.nome_do_personagem)&&
        (data.nome_do_jogador != '??' && data.nome_do_personagem != '??')){
            return (
                <h2>Qual é {data.nome_do_jogador} pensa no {data.nome_do_personagem}
                </h2>
            )
        } else {
            return <h2>Qual é, da uma chance pra ele tadinho :(</h2>
        }
    }
    var deleter = () => {
        localStorage.setItem('ficha_', JSON.stringify({gasta_tudo: 0}));
        window.location.reload();
    }

    const [haveSure, setSure] = useState(false);
    const [haveRealSure, setRealSure] = useState(false);
    const [haveReally, setReally] = useState(false);
    const [Absolutully, setAbsolutully] = useState(false);
    const [lastOne, setLast] = useState(false);
    return (
        <div className="clicker">
            <label onClick={()=>{setSure(true)}} >Clear</label>
            <Popup trigger={haveSure} setTrigger={setSure} tam={[220, 380]}
            clicker={()=>window.location.reload()}>
                <div className="SureSure">
                    <h2>Você gostaria de deletar o save local?</h2>
                    <button onClick={()=>setRealSure(true)}>Sim</button>
                    <button onClick={()=>window.location.reload()}>Não</button>
                </div>
            </Popup>
            <Popup trigger={haveRealSure} setTrigger={setRealSure} tam={[260, 380]}
            clicker={()=>window.location.reload()}>
                <div className="SureSure">
                    <h2>Isso vai remover o seu personagem, prosseguir e deletar?</h2>
                    <button onClick={()=>setReally(true)}>Sim</button>
                    <button onClick={()=>window.location.reload()}>Não</button>
                </div>
            </Popup>
            <Popup trigger={haveReally} setTrigger={setReally} tam={[220, 380]}
            clicker={()=>window.location.reload()}>
                <div className="SureSure">
                    <h2>Tem certeza disso?</h2>
                    <button onClick={()=>window.location.reload()}>Não</button>
                    <button onClick={()=>setAbsolutully(true)}>Sim</button>
                </div>
            </Popup>
            <Popup trigger={Absolutully} setTrigger={setAbsolutully} tam={[240, 330]}
            clicker={()=>window.location.reload()}>
                <div className="SureSure">
                    {func()}
                    <button onClick={()=>setLast(true)} style={{fontSize:'25px'}}>Deletar</button>
                    <button onClick={()=>window.location.reload()} style={{fontSize:'25px'}}>
                        Voltar</button>
                </div>
            </Popup>
            <Popup trigger={lastOne} setTrigger={setLast} tam={[240, 330]}
            clicker={()=>window.location.reload()}>
                <div className="SureSure">
                    <h2>Ta bom esse é o ultimo, tem certeza?</h2>
                    <button onClick={()=>deleter()}>Sim</button>
                    <button onClick={()=>window.location.reload()}>Não</button>
                </div>
            </Popup>
        </div>
    );
}