import React from 'react'
import listStyle from '../style/listStyle.css'
import ByeItems from './ByeItems.jsx'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    data = data.inventory;

    if (data != undefined){
        return (
            <ul className="InvList">
                {
                    data.map((item, index)=>{
                        return (
                            <li>
                                <p>{item.name}</p>
                                <label>{item.heavy}</label>
                                <ByeItems index={index} inv="inventory"/>
                            </li>
                        )
                    })
                    }
            </ul>
        )
    } return (<></>);

}