import React from 'react'
import Score from './subconponents/Score.jsx'
import ScoreInv from './subconponents/ScoreInv.jsx'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    if (!data.gasta_tudo){
        data['gasta_tudo'] = 0;
        localStorage.setItem('ficha_', JSON.stringify(data));
    }

    var define = () => {
        var data = JSON.parse(localStorage.getItem('ficha_'));
        const astro = {
            calc:['VIDA_MAXIMA', 'INVENTARIO_MAXIMO', 'SAN_MAXIMA', 'MA_MAXIMA'],
            adc: ['VIDA_ADICIONAL', 'INVENTARIO_ADICIONAL', 'SAN_ADICIONAL', 'MA_ADICIONAL'],
            sun: [
                ((Number(data.constituição) + Number(data.tamanho)) / 5 || 0),
                ((Number(data.força) + Number(data.tamanho)) / 5 || 0),
                (Number(data.poder) || 0),
                (Number(data.constituição) + Number(data.poder) / 3 || 0)
            ]
        }

        var i = 0;

        for (var item of astro.calc){
            if (data[astro.adc[i]] == '??'){
                data[astro.adc[i]] = 0;
            }
            
            data[item] = Math.round(Number(astro.sun[i]) + Number(data[astro.adc[i]] || 0));
            ++i;
        }
        localStorage.setItem('ficha_', JSON.stringify(data));
    }
    define();
    return (
        <div id="Points">
            <div id="Score">
                <Score inf_req="VIDA"/>
                <Score inf_req="SAN"/>
                <Score inf_req="MA"/>
                <ScoreInv inf_req="INV"/>
            </div>
        </div> 
    )
}