import React from 'react'
import style from './../style/form.css'
import img from './../../icons/save.png'
import gunsCss from './../style/guns.css'
import Combat from '../data/Combat.json'
import pList from './../style/pList.css'

export default (req) => {
    const item = req.haveItem;
    var placeholders = ['Nome da Arma', 'Peso da Arma', 'Dado de Dano', 'Munição']
    if (req.haveItem && req.haveItem != undefined){
        placeholders = [item.name, item.heavy, item.dice, item.ammo, item.type];
    }
    return (
        <div className="form">
            <div>
                <label>Nome:</label>
                <input className="input-form" type="text" placeholder={placeholders[0]}></input>
            </div>
            <div>
                <label>Peso:</label>
                <input className="input-form" type="number" placeholder={placeholders[1]}></input>
            </div>
            <div>
                <label className="dice-title">Dado:</label>
                <input className="input-form dmg-dice" type="text" placeholder={placeholders[2]}>
                </input>
            </div>
            <div>
                <label>Munição:</label>
                <input className="input-form" type="number" placeholder={placeholders[3]}></input>
            </div>
            <div>
                <label>Tipo:</label>
                <select className="input-form">
                    {Combat.map(item=>{
                        return (
                            <option key={item} selected={item.toUpperCase() == placeholders[4]}>
                                {item.toUpperCase()}
                            </option>
                        )
                    })}
                </select>
            </div>
        </div>
    )
}