import React from 'react'
import { useState } from 'react'
import Popup from './Popup.jsx'
import oopscss from './../style/oopscss.css'
import pointcss from './../style/pointcss.css'

export default (req) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const [trigger, setFunc] = useState(false);
    
    var calculator = (calc)=> {
        var input = document.getElementsByClassName(`numValue${req.inf_req}`)[0].value || 0;
        var dataPos = data[`${req.inf_req}`];
        if (dataPos == null){dataPos = 0}
        var sin = {
            add: parseFloat(dataPos) + (parseFloat(input) || 0),
            rem: parseFloat(dataPos) - (parseFloat(input) || 0)
        }

        if (sin[calc] > data[`${req.inf_req}_MAXIMA`]){
            return setFunc(true);
        }
        data[`${req.inf_req}`] = sin[calc];
        localStorage.setItem('ficha_', JSON.stringify(data));
        return window.location.reload();
    }

    return (
        <div className="detail roll">
            <p>{req.inf_req}:</p>
            <button onClick={()=>calculator('add')}>+</button>
            <button onClick={()=>calculator('rem')}>-</button>
            <input placeholder="XX" className={`numValue${req.inf_req}`} 
            onWheel={(e)=>e.target.blur()}></input>
            <div className="ret" >
                <p>{data[`${req.inf_req}`] || '??'} / {data[`${req.inf_req}_MAXIMA`] || '??'}</p>
            </div>

            <Popup trigger={trigger} setTrigger={setFunc} tam={[125, 250]} clicker={
                ()=>{window.location.reload()}
            }>
                <div>
                    <label>
                        Infelizmente você ultrapassou o maximo da capacidade de {req.inf_req}
                    </label>
                </div>
            </Popup>
        </div>
    )
}