export default (inv) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const inputs = document.querySelectorAll('.input-form');
 
    if (!data[inv]){
        data[inv] = [];
    }

    var info = data;

    try {
        for (var i of data[inv]){
            if (inputs[0].value == i.name){
                inputs[0].value = inputs[0].value+"_copia";
            }
        }
        var json = {
            name: inputs[0].value,
        };
        if (inv == 'guns'){
            json['heavy'] = inputs[1].value;
            json['dice'] = inputs[2].value;
            json['ammo'] = inputs[3].value;
            json['type'] = inputs[4].value;
        }
        if (inv == 'power'){
            json['dice'] = inputs[2].value;
            json['ma'] = inputs[1].value;
            json['desc'] = document.getElementsByClassName('textarea-form')[0].value;
        } else {
            json['heavy'] = inputs[1].value;
        }
        info[inv].push(json);
    } catch { return window.location.reload(); }
    info.gasta_tudo = parseFloat(data.gasta_tudo) + parseFloat(inputs[1].value);
    
    if ((!data.INVENTARIO_MAXIMO || data.INVENTARIO_MAXIMO == undefined) || 
    (parseFloat(inputs[1].value) < 0)){
        return [true, 1]
    }

    if (info.gasta_tudo > Number(data.INVENTARIO_MAXIMO) && inv != 'power'){
        return [true, 0];
    }
    localStorage.setItem(`ficha_`, JSON.stringify(info));
    return window.location.reload();
} 