import React from 'react'

export default (req) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    var cutDetail = (det) =>{
        var i = 0;
        var full = '';
        if (!det || det == ''){
            return false;
        }

        for (let car of det){
            if (i == 22){
                if (det.length > 22){
                    full += '...'
                }
                break;
            }
            full += car;
            i++;
        }
        return full;
    }
    return (
        <div className="detail">
            <label>{req.inf_req}:</label>
            <p>{
                cutDetail(data[req.inf_req.replace(' ', '_').toLowerCase()]) || '?????'
            }</p>
        </div>  
    )
}