import React from 'react'
import css from './../style/writeblock.css'
import listenAll from '../../backend/listenAll.js'

export default (req) =>{
    var data = JSON.parse(localStorage.getItem('ficha_'));

    var saves = ['desc_pessoal', 'pessoas_importantes', 'feridas', 'medos', 'doencas', 'notes'];

    if (data.historico == undefined){
        data.historico = {};
        for (var save of saves){
            data.historico[save] = '';
        }
    }

    return (
        <div className="writeblock">
            <p>{req.back}:</p>
            <textarea className={req.save} onChange={()=>listenAll()} 
            defaultValue={data.historico[req.save]}>
            </textarea>
        </div>
    )
}