import React from 'react' 

export default () => {
    var func = () =>{
        var portrait = JSON.parse(localStorage.getItem('ficha_')).retrato;
        if (portrait != '??'){
            return (
                <img src={JSON.parse(localStorage.getItem('ficha_')).retrato}/>
            )
        } return;
    }
    return (
        <div className="pfp">
            <div className="profile-frame">
                {func()}
            </div>
        </div>
    )
}