import React from 'react'
import style from './../style/form.css'
import img from './../../icons/save.png'
import pListcss from './../style/pList.css'

export default (req) => {
    const item = req.haveItem 
    var placeholders = ['Nome da Magia', 'MA Gasta', 'Dado de Dano']
    if (req.haveItem && req.haveItem != undefined){
        placeholders = [item.name, item.ma, item.dice, item.desc];
    }
    return (
        <div className="form">
            <div>
                <label>Nome:</label>
                <input className="input-form" type="text" placeholder={placeholders[0]}></input>
            </div>
            <div>
                <label>MA:</label>
                <input className="input-form" type="number" placeholder={placeholders[1]}></input>
            </div>
            <div>
                <label className="dice-title">Dado:</label>
                <input className="input-form dmg-dice" type="text" placeholder={placeholders[2]}>
                </input>
            </div>
            <div>
                <label className="dice-title">Descrição:</label>
                <textarea className="textarea-form" type="text" placeholder={'Descriçao da Magia'}
                defaultValue={placeholders[3]}>
                </textarea>
            </div>
        </div>
    )
}