import React from 'react'
import { useState } from 'react'
import combatData from './data/Combat.json'
import Roll from './subconponents/Roll'
import img from './../icons/d20.png'
import attk from './../icons/target.png'
import Popup from './subconponents/Popup'
import DoABarrelRoll from './subconponents/DoABarrelRoll'
import mimPer from './data/minPer.json'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const [rollPer, setRollPer] = useState(false);
    const [rollDmg, setRollDmg] = useState(false);

    return (
        <div id="Combat">
            <h2>Combate</h2>
            <div id="Attack" className="roll">
                <p>CORPO A CORPO</p>
                <div className="aBuckett">
                    <button className="d20" onClick={()=>{setRollPer(true)}}>
                        <img src={img}/> 
                    </button>
                    <button className="d20" onClick={()=>{setRollDmg(true)}}>
                        <img src={attk}/> 
                    </button>
                </div>
            </div>
            {combatData.map((item)=>{
                    return (<Roll inf_req={item.toUpperCase()} 
                    local={data[item.replaceAll(' ', '_')]}
                    alt={'combate_com_'+item.replaceAll(' ', '_')}></Roll>)
            })}

            <Popup trigger={rollPer} setTrigger={setRollPer} tam={[235, 300]}>
                <DoABarrelRoll per={(data['soco/chute'] || 25)} complex={true}/>
            </Popup>
            <Popup trigger={rollDmg} setTrigger={setRollDmg} tam={[235, 350]}>
                <DoABarrelRoll per={'1d3'} complex={'1d3'}/>
            </Popup>
        </div>
    )
}