import React from 'react'
import { useState } from 'react'
import Popup from './Popup.jsx'
import oopscss from './../style/oopscss.css'
import pointcss from './../style/pointcss.css'
import RenderRightError from './../../backend/renderRightError.jsx'

export default (req) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const [trigger, setFunc] = useState(false);
    const [setError, setFuncError] = useState(false);
    
    var calculator = (calc)=> {
        var input = document.getElementsByClassName('numValue')[0].value;

        var dataPos = data.gasta_tudo;
        var sin = {
            add: parseFloat(dataPos) + parseFloat(input),
            rem: parseFloat(dataPos) - parseFloat(input)
        }

        if (sin[calc] > data.INVENTARIO_MAXIMO){
            return setFunc(true);
        } else if (!data.INVENTARIO_MAXIMO || sin[calc] < 0){
            return setFuncError(true);
        }
        data.gasta_tudo = sin[calc];
        localStorage.setItem('ficha_', JSON.stringify(data));
        return window.location.reload();
    }

    return (
        <div className="detail roll">
            <p>{req.inf_req}:</p>
            <button onClick={()=>calculator('add')}>+</button>
            <button onClick={()=>calculator('rem')}>-</button>
            <input placeholder="XX" className={`numValue`}></input>
            <div className="ret">
                <p>{data.gasta_tudo || '??'} / {data.INVENTARIO_MAXIMO || '??'}</p>
            </div>

            <Popup trigger={trigger} setTrigger={setFunc} tam={[110, 250]} clicker={
                ()=>{window.location.reload()}
            }>
                <div>
                    <label>
                        Infelizmente você ultrapassou o maximo da capacidade de {req.inf_req}
                    </label>
                </div>
            </Popup>
            <Popup trigger={setError} setTrigger={setFuncError} tam={[150, 450]}>
                <RenderRightError/>
            </Popup>
        </div>
    )
}