import React from 'react'

export default (req) => {
    var removeItem = (index) => {
        var data = JSON.parse(localStorage.getItem('ficha_'));
        var info = data[req.inv];
        if (data['power'] != data[req.inv]){
            data.gasta_tudo -= info[index].heavy;
        }
        info.splice(index, 1);
        data[req.inv] = info;
        localStorage.setItem('ficha_', JSON.stringify(data));
        window.location.reload();
    }
    return (
        <button className="byebye" onClick={()=>removeItem(req.index)}>X</button>
    )
}