import React from 'react'
import listStyle from '../style/listStyle.css'
import ByeItems from './ByeItems.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'
import { useState } from 'react'
import Popup from './Popup.jsx'
import img from '../../icons/d20.png'
import redimg from '../../icons/target.png'
import ammoImg from '../../icons/ammo.png'
import popupcss from '../style/popup.css'
import dist from './../style/distorcionDBR.css'
import saveImg from './../../icons/save.png'
import GunList from './GunList'

export default () => {
    const [errorBtt, setErrorBtt] = useState(false);
    var saved = JSON.parse(localStorage.getItem('ficha_'));
    var data = saved.guns;
    var nonFullName = (name) => {
        name = name.toUpperCase();
        var returnName = '';
        for (let i=0; i<=13; i++){
            if (!name[i]){
                break;
            }
            returnName += name[i];
            if (i == 13){
                returnName += '...';
            }
        }
        return returnName;
    }
    
    var edit = () => {  
        var docElement = document.getElementsByClassName('dmg-dice')[0];
        docElement = docElement.value || docElement.placeholder;

        if (docElement.indexOf('d') == -1){
            return setErrorBtt(true);
        };

        var docValues = [
            document.getElementsByClassName('input-form')[0],
            document.getElementsByClassName('input-form')[1],
            document.getElementsByClassName('input-form')[2],
            document.getElementsByClassName('input-form')[3],
            document.getElementsByClassName('input-form')[4]
        ]

        var info = JSON.parse(localStorage.getItem('ficha_'));
        for (var i = 0; i < info.guns.length; i++){
            if (info.guns[i].name == docValues[0].placeholder){
                var ind = ['name', 'heavy', 'dice', 'ammo', 'type'];
                var old = info.guns[i];
                for (let c = 0; c < docValues.length; c++){
                    old[ind[c]] = docValues[c].value || docValues[c].placeholder;
                }
                info.guns[i] = old;
                localStorage.setItem('ficha_', JSON.stringify(info));
                return window.location.reload();
            }
        }
    }
    if (data != undefined){
        return (
            <ul className="InvList">
                {
                    data.map((item, index)=>{
                        const [buttonRollDice, setButtonRollDice] = useState(false);
                        const [buttonRollPer, setButtonRollPer] = useState(false);
                        const [editGun, setEditGun] = useState(false);
                        var saved = JSON.parse(localStorage.getItem('ficha_'));
                        return (
                            <li>
                                <div className="editGun">
                                    <button onClick={()=>setEditGun(true)}>Editar</button>
                                </div>
                                <p className="gun-inp gun-title">{nonFullName(item.name)}</p>
                                <div className="gun-ndrow">
                                    <label className="gun-inp">{item.heavy}p</label>
                                    <div className="ammo-inp">
                                        <img src={ammoImg} name="ammo"></img>
                                        <label className="gun-inp">{item.ammo}</label>
                                    </div>
                                    <label className="gun-inp">{item.dice}</label>
                                    <div className="bttsstuff">
                                        <button className="d20" onClick={()=>{
                                            setButtonRollDice(true)
                                            }}>
                                            <img src={redimg}/> 
                                        </button>
                                        <button className="d20" onClick={()=>{
                                            setButtonRollPer(true)
                                            }}>
                                            <img src={img}/> 
                                        </button>
                                    </div>
                                </div>
                                <ByeItems index={index} inv="guns"/>
                                <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} 
                                tam={[235, 300]}>
                                    <DoABarrelRoll per={item.dice} complex={item.dice}/>
                                </Popup>

                                <Popup trigger={buttonRollPer} setTrigger={setButtonRollPer} 
                                tam={[235, 300]}>
                                    <DoABarrelRoll 
                                per={saved[item.type.toLowerCase().replaceAll(' ', '_')]} 
                                complex={true}/>
                                </Popup>
                                <Popup trigger={editGun} setTrigger={setEditGun}
                                tam={[320, 450]}>
                                    <button className="save">
                                        <img src={saveImg} onClick={edit}/>
                                    </button>
                                    <h2>EDITAR {item.name}</h2>
                                    <GunList haveItem={item}/>
                                </Popup>
                                <Popup trigger={errorBtt} setTrigger={setErrorBtt} tam={[200, 450]}>
                                    <div className="max-flex">
                                        <h2>Preencha um numero valido</h2>
                                        <p>Preencha o campo de dano com o modelo de dado 
                                            Numero+d+Dano
                                            <br/>Ex:2d3 (Dois dados de 3)
                                        </p>
                                    </div>
                                </Popup>
                            </li>
                        )
                    })
                    }
            </ul>
        )
    } return (<></>);
}