import React from 'react'
import listStyle from '../style/listStyle.css'
import ByeItems from './ByeItems.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'
import { useState } from 'react'
import Popup from './Popup.jsx'
import img from '../../icons/target.png'
import aImg from '../../icons/d20.png'
import popupcss from '../style/popup.css'
import distcss from './../style/distorcionDBR.css'
import PowerList from './PowerList.jsx'
import saveImg from './../../icons/save.png'
import LoadBalls from '../../backend/LoadAmiBalls'

export default () => {
    const [ errorBtt, setErrorBtt ] = useState(false);
    var saved = JSON.parse(localStorage.getItem('ficha_'));
    var data = saved.power;

    var nonFullName = (name) => {
        name = name.toUpperCase();
        var returnName = '';
        for (let i=0; i<=13; i++){
            if (!name[i]){
                break;
            }
            returnName += name[i];
            if (i == 13){
                returnName += '...';
            }
        }
        return returnName;
    }

    var edit = () => {  
        var docElement = document.getElementsByClassName('dmg-dice')[0];
        docElement = docElement.value || docElement.placeholder;

        if (docElement.indexOf('d') == -1){
            return setErrorBtt(true);
        };

        var docValues = [
            document.getElementsByClassName('input-form')[0],
            document.getElementsByClassName('input-form')[1],
            document.getElementsByClassName('input-form')[2],
            document.getElementsByClassName('textarea-form')[0]
        ]

        var info = JSON.parse(localStorage.getItem('ficha_'));
        for (var i = 0; i < saved.power.length; i++){
            if (info.power[i].name == docValues[0].value || docValues[0].placeholder){
                var ind = ['name', 'ma', 'dice','desc']
                var old = info.power[i];
                for (let c = 0; c < docValues.length; c++){
                    old[ind[c]] = docValues[c].value || docValues[i].placeholder;
                }
                info.power[i] = old;
                localStorage.setItem('ficha_', JSON.stringify(info));
                return window.location.reload();
            }
        }
    }
    if (data != undefined){
        return (
            <ul className="InvList">
                {
                    data.map((item, index)=>{
                        const [buttonRollDice, setButtonRollDice] = useState(false);
                        const [buttonRollPer, setButtonRollPer] = useState(false);
                        const [editPower, setEditPower] = useState(false);

                        return (
                            <li>
                                <div className="editPower">
                                    <button onClick={()=>setEditPower(true)}>Editar</button>
                                </div>
                                <div className="pWrapper">
                                    <p>{nonFullName(item.name)}</p>
                                </div>
                                <ByeItems index={index} inv="power"/>   
                                <div className="power-desc">
                                    <p>{item.desc}</p>
                                </div>
                                <label className="gun-inp">{item.dice}</label>
                                <label className="gun-inp">{item.ma}ma</label>

                                <div className="bttsstuff">
                                    <button className="d20" onClick={()=>{setButtonRollDice(true)}}>
                                        <img src={img}/> 
                                    </button>
                                    <button className="d20" onClick={()=>{setButtonRollPer(true)}}>
                                        <img src={aImg}/> 
                                    </button>
                                </div>
                                <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} 
                                tam={[235, 320]}>
                                    <DoABarrelRoll per={item.dice} complex={item.dice}/>
                                </Popup>
                                <Popup trigger={buttonRollPer} setTrigger={setButtonRollPer} 
                                tam={[235, 320]}>
                                    <DoABarrelRoll per={saved.aproximação_irreal} 
                                    complex={true}/>
                                </Popup>
                                <Popup trigger={editPower} setTrigger={setEditPower}
                                tam={[370, 550]}>
                                    <button className="save">
                                        <img src={saveImg} onClick={()=>edit(item)}/>
                                    </button>
                                    <h2>EDITAR {item.name}</h2>
                                    <PowerList haveItem={item} />
                                </Popup>
                                <Popup trigger={errorBtt} setTrigger={setErrorBtt} tam={[200, 450]}>
                                    <div className="max-flex">
                                        <h2>Preencha um numero valido</h2>
                                        <p>Preencha o campo de dano com o modelo de dado 
                                            Numero+d+Dano
                                            <br/>Ex:2d3 (Dois dados de 3)
                                        </p>
                                    </div>
                                </Popup>
                            </li>
                        )
                    })
                    }
            </ul>
        )
    } return (<></>);
}