import minValues from './../components/data/minPer.json'
import Attributes from './../components/data/Attributes.json'

export default (jsonReq) => {
    var madePlaceholder = (ind, res) => {
        var att = minValues[Attributes[ind]];
        if (Number(att)+Number(res) > 100){
            return 99;
        }
        return Number(att)+Number(res);
    }

    var clearLocalStorage = (json) => {
        var obj_keys = Object.keys(json);
        var result = {};
        obj_keys.map((inf)=>{
            result[inf] = json[inf];
        });
        return result;
    }

    const inputs = document.querySelectorAll('.input-form');
    var formData = require(`./../components/data/${jsonReq}`);
    var data = JSON.parse(localStorage.getItem(`ficha_`));

    if (!data){
        data = localStorage.setItem(`ficha_`, '[]');
    }

    var info = clearLocalStorage(data);
    
    var i = 0;
    for (var input of inputs){

        var res = input.value;
        
        if ((input.type == "number" && res > 100) && !(i >= 16 && i <= 20)){ res = 0 }

        else if (res == '' || res == ' '){
            res = input.placeholder;
        }

        info[Attributes[i]] = madePlaceholder(i, res);
        i++
    }
    localStorage.setItem(`ficha_`, JSON.stringify(info));
    window.location.reload();
} 