import React from 'react'
import { useState } from 'react'
import img from './../../icons/d20.png'
import Popup from './Popup.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'
import distcss from './../style/distorcionDBR.css'
import mimPer from './../data/minPer.json'

export default (req) => {
    const [buttonRollDice, setButtonRollDice] = useState(false);
    var defaultValue = () => {
        var local = req.local || mimPer[req.alt.replaceAll('_', ' ')] || 'XX';
        if (Number(local) < 10){
            return '0'+local;
        } return local;
    }
    return (
        <div className="detail roll">
            <p className="roll-title">{req.inf_req}:</p>
            <label>{defaultValue()}</label>
            <button className="d20" onClick={()=>{setButtonRollDice(true)}}>
                <img src={img}/> 
            </button>

            <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} tam={[235, 300]}>
                <DoABarrelRoll per={Number(req.local)}
                complex={true}/>
            </Popup>
        </div>  
    )
}