export default async ()=>{
    var data = JSON.parse(localStorage.getItem('ficha_'));
    var histSave = ['desc_pessoal', 'pessoas_importantes', 'feridas', 'medos', 'doencas', 'notes'];
    var checkSave = ['lesao_grave', 'inconsciente', 'morrendo', 'traumatizado', 'enloquecido', 
    'agarrado']

    data.historico = {};
    data.checkbox = {};
    for (let pos of histSave){
        data.historico[pos] = document.getElementsByClassName(pos)[0].value;
    }
    for (let pos of checkSave){
        data.checkbox[pos] = document.getElementsByClassName(pos)[0].checked;
    }
    return localStorage.setItem('ficha_', JSON.stringify(data));
}