import React from 'react'
import style from './../style/popup.css'
import css from './../style/doabarrelroll.css'

export default (req) => {
    var tam = req.tam;
    if (!req.tam){
        tam = [570, 690]
    }
    return (req.trigger) ? (
        <div id="edit-popup">
            <div className="popup-box" style={{
                height: tam[0]+'px',
                width: tam[1]+'px'
            }}>
                <button className="close-popup" onClick={()=>{
                    req.setTrigger(false)
                    if (req.clicker) {
                        req.clicker();
                    }
                }
                }>X</button>
                {req.children}
            </div>
        </div>
    ) : "";
}