import Checkbox from './subconponents/Checkbox.jsx'
import Pfp from './subconponents/Pfp.jsx'
import React from 'react'
import {useState} from 'react'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    if (!data || data == undefined){
        localStorage.setItem('ficha_', '[]');
        data = {
            nome_do_personagem: "?????",
            nome_do_jogador: "?????"
        }
    }

    var cutName = (name, placeholder) => {
        if (name == undefined){
            return placeholder;
        }
        
        var i = 0;
        var finalName = '';
        try {
            for (var letter of name){
                finalName += letter;
                if (letter == ' '){
                    finalName += name[i+1]+'.';
                    break;
                }
                i++;
            }
            return finalName;
        } catch {
            return name;
        }
    }

    return (
        <div id="profile">
            <img src={''}></img>
            <Pfp />
            <div className="nametags">
                <div  className="caract_name">
                    <p>
                        {cutName(data.nome_do_personagem, '???????????')}
                    </p>
                </div>
                <div className="player_name">
                    <p>
                        {cutName(data.nome_do_jogador, '??????')}
                    </p>
                </div>
            </div>
            <div className="checkbox_container">
                <Checkbox status="Lesão Grave" order={'lesao_grave'}/>
                <Checkbox status="Inconsciente" order={'inconsciente'}/>
                <Checkbox status="Morrendo" order={'morrendo'}/>
                <Checkbox status="Traumatizado" order={'traumatizado'}/>
                <Checkbox status="Enloquecido" order={'enloquecido'}/>
                <Checkbox status="Agarrado" order={'agarrado'} />
            </div>
        </div>
    )
}
