import React from 'react'
import Roll from './Roll.jsx'
import att from './../data/Attributes.json'

export default () => {
    var storage = JSON.parse(localStorage.getItem('ficha_'));

    var jsx = att.map((Exp)=>{
        if (Exp.startsWith('combate com ')){
            return;
        }
        return <Roll inf_req={Exp} 
        local={storage[(Exp)]} 
        alt={(Exp.replaceAll(' ', '_'))}/>
    });
    return <div>{jsx}</div>
}