export default () =>{
    let data = JSON.parse(localStorage.getItem('ficha_'));
    return (!data.INVENTARIO_MAXIMO) ? 
        (
            <div className="max-flex">
                <h2>Inventario maximo não foi definido</h2>
                <p>O valor atual do inventario maximo é 0</p>
            </div>
        ) : (
            <div className="max-flex">
                <h2>Numero Invalido</h2>
                <p>O peso desse item só poder ser entre 0 e {
                    Number(data.INVENTARIO_MAXIMO) - Number(data.gasta_tudo)
                }</p>
            </div>
        )
}