import React from 'react'
import style from './../style/form.css'
import formData from './../data/Form.json'
import formDataV2 from './../data/FormV2.json'
import img from './../../icons/save.png'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    var placeholder = '?????';
    var i = 0;

    return (
        <div className="form">
            {
                formData.map((quote)=> {
                    var pos = quote.replaceAll(' ', '_');
                    placeholder = data[`${pos}`] || '??';
                    i++;
                    const ind = i-1;
                    return (formDataV2[ind].type != 'option') ? 
                    (
                        <div>
                            <p>{quote}:</p>
                            <input type={formDataV2[ind].type}
                            onWheel={(e)=>e.target.blur()}
                            placeholder={placeholder}
                            className="input-form">
                            </input>
                        </div> ) : (
                        <div>
                            <p>{quote}:</p>
                            <select className="input-form">
                                <option selected={data.sexo == 'Masculino'}>Masculino</option>
                                <option selected={data.sexo == 'Feminino'}>Feminino</option>
                            </select>
                        </div>
                    )
                })
            }
        </div>
    )
}