import React from 'react'
import css from './style/popup.css'
import { useState } from 'react'
import Popup from './subconponents/Popup.jsx'
import saveImg from '../icons/save.png'
import GunList from './subconponents/GunList.jsx'
import GunItem from './subconponents/GunItem.jsx'
import LoadBalls from './../backend/LoadAmiBalls.js'
import RenderRightError from './../backend/renderRightError.jsx'

export default () => {
    const [ buttonGun, setButtonGun ] = useState(false);
    const [ setMax, setMaxFunc ] = useState(false);
    const [ errorBtt, setErrorBtt ] = useState(false);
    const [setError, setErrorFunc] = useState(false);

    var data = JSON.parse(localStorage.getItem('ficha_'));

    var addOnClick = () => {
        var docElement = document.getElementsByClassName('dmg-dice')[0].value;

        if (docElement.indexOf('d') == -1){
            return setErrorBtt(true);
        };

        let lod = LoadBalls('guns');

        if (lod[0] == true){
            if (lod[1] == 0){
                return setMaxFunc(true);
            } return setErrorFunc(true);
        }
    }

    return (
        <div id="Guns" className="ScrollInv">
            <h2>Armas</h2>
            <div className="list">
                <GunItem />
            </div>
            <button className="popup-btn" onClick={()=>setButtonGun(true)}>+</button> 
            <Popup trigger={buttonGun} setTrigger={setButtonGun} tam={[320, 450]}>
                <button className="save">
                    <img src={saveImg} onClick={addOnClick}/>
                </button>
                <h2>ADICIONAR ARMA</h2>
                <GunList />
            </Popup>
            <Popup trigger={setMax} setTrigger={setMaxFunc}  tam={[150, 450]}>
                <div className="max-flex">
                    <h2>Seu inventario esta cheio</h2>
                    <p>Sua capacidade maxima de peso é {data.INVENTARIO_MAXIMO}</p>
                </div>
            </Popup>
            <Popup trigger={errorBtt} setTrigger={setErrorBtt}  tam={[200, 450]}>
                <div className="max-flex">
                    <h2>Preencha um numero valido</h2>
                    <p>Preencha o campo de dano com o modelo de dado Numero+d+Dano
                        <br/>Ex:2d3 (Dois dados de 3)
                    </p>
                </div>
            </Popup>
            <Popup trigger={setError} setTrigger={setErrorFunc} tam={[150, 450]}>
                <RenderRightError/>
            </Popup>
        </div>
    )
}