import React from 'react'
import css from './style/popup.css'
import { useState } from 'react'
import Popup from './subconponents/Popup.jsx'
import saveImg from '../icons/save.png'
import LoadBalls from '../backend/LoadAmiBalls.js'
import InventoryPopup from './subconponents/InventoryPopup.jsx'
import InvItem from './subconponents/InvItem.jsx'
import maxFlexCss from './style/maxFlex.css'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    const [ buttonInventory, setButtonInventory ] = useState(false);
    const [ setError, setErrorFunc ] = useState(false);
    const [ setMax, setMaxFunc ] = useState(false);

    var addOnClick = () => {
        let lod = LoadBalls('inventory');
        if (lod[0] == true){
            if (lod[1] == 0){
                return setMaxFunc(true);
            } return setErrorFunc(true);
        }
    }
    
    var renderRightError = () =>{
        return (!data.INVENTARIO_MAXIMO) ? 
            (
                <div className="max-flex">
                    <h2>Inventario maximo não foi definido</h2>
                    <p>O valor atual do inventario maximo é 0</p>
                </div>
            ) : (
                <div className="max-flex">
                    <h2>Numero Invalido</h2>
                    <p>O peso desse item só poder ser entre 0 e {
                        Number(data.INVENTARIO_MAXIMO) - Number(data.gasta_tudo)
                    }</p>
                </div>
            )
    }

    return (
        <div id="Inventory" className="ScrollInv">
            <h2>Inventario</h2>
            <div className="list">
                <InvItem />
            </div>
            <button className="popup-btn" onClick={()=>setButtonInventory(true)}>+</button>
            <Popup trigger={buttonInventory} setTrigger={setButtonInventory} tam={[250, 380]}>
                <button className="save">
                    <img src={saveImg} onClick={addOnClick}/>
                </button>
                <h2>ADICIONAR ITEM</h2>
                <InventoryPopup/>
            </Popup>
            <Popup trigger={setMax} setTrigger={setMaxFunc} tam={[150, 450]}>
                <div className="max-flex">
                    <h2>Seu inventario esta cheio</h2>
                    <p>Sua capacidade maxima de peso é {data.INVENTARIO_MAXIMO}</p>
                </div>
            </Popup>
            <Popup trigger={setError} setTrigger={setErrorFunc} tam={[150, 450]}>
                {renderRightError()}
            </Popup>
        </div>
    )
}